<template>
    <div class="account">
        <h1 class="account--header">{{ user.fullName }} - Account</h1>
        
        <nav class="account--nav">
            <div class="account--nav-item" v-if="contacts" @click="()=>this.activeTab = 'CONTACTS'">Contacts</div>
            <div class="account--nav-item" v-if="preferences" @click="()=>this.activeTab = 'NOTIFICATIONS'">Notification Preferences</div>
        </nav>

        <section class="account--prefs">

            <div v-if="contacts && activeTab === 'CONTACTS'">
                <User v-for="user in contacts" :key="user.id" :user="user"/>
            </div>

            <div v-if="preferences  && activeTab === 'NOTIFICATIONS'">
                <h4>Email Delivery Type</h4>
                <label>
                    Never Send Email
                    <input type="radio" v-model="preferences.emailDeliveryType" value="NONE"/>
                </label>
                <label>
                    Send Immediately
                    <input type="radio" v-model="preferences.emailDeliveryType" value="IMMEDIATE"/>
                </label>
                <label>
                    Batch Notifications
                    <input type="radio" v-model="preferences.emailDeliveryType" value="BATCH"/>
                </label>
                <div v-if="preferences.emailDeliveryType === 'BATCH'">
                    <h4>Batch Email Frequency</h4>
                    <input type="range" step="5" min="5" max="120" v-model="preferences.emailFrequency"/>
                    {{ preferences.emailFrequency }}
                </div>
            </div>

        </section>

    </div>
</template>

<script>
import api from "./../../api.js";
import store from "./../../store.js";

import User from "./../User.vue";

export default {
  name: "Organization",
  components: {
    User
  },
  data() {
    return {
      preferences: null,
      contacts: null,

      activeTab: "CONTACTS"
    };
  },
  computed: {
    user() {
      return store.state.user;
    }
  },
  created() {
    if (!this.user) {
      console.log("no user");
      return;
    }

    api
      .getAll(`/users/${this.user.id}/contacts`)
      .then(result => {
        this.contacts = result;
      })
      .catch(err => console.log(err));

    api
      .getAll(`/users/${this.user.id}/notification-settings`)
      .then(result => {
        this.preferences = result;
      })
      .catch(err => console.log(err));
  },
  methods: {}
};
</script>

<style lang="scss" scoped>
@import "./../../scss/vars.scss";

.account {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-template-rows: min-content 1fr;
  grid-template-areas:
    "header header"
    "nav content";
}

.account--header {
  grid-area: header;
}

.account--nav {
  grid-area: nav;
  overflow-y: auto;
}

.account--prefs {
  grid-area: content;
  overflow-y: auto;
}
</style>

 


